<template>
    <yd-layout class="box_recheck">
        <navBar title="随手拍隐患复查" slot="navbar"></navBar>
        <div class="box_contc" v-for="data,index in data" :key="index">
            <div class="box_titlec">整改信息</div>
            <div class="flex"><span class="gray">整改金额：</span>{{data.ZG_MONEY}}元</div>
            <div class="flex"><span class="gray">整改人员：</span>{{correctname}}</div>
            <div class="flex"><span class="gray">整改时间：</span>{{data.ZG_TIME}}</div>
            <div class="flex"><span class="gray">整改备注：</span><div style="flex: 1;">{{data.ZG_REMARKS}}</div></div>
            <div class="flex">
                <div class="gray">整改图片：</div>
                <div class="flex_image">
                    <div class="flex_image_img" @click="onPressLightbox($event,data.ZG_ATTACHMENT)">
                        <img :src="data.ZG_ATTACHMENT" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="box_form">
            <div class="box_titlec" style="color: orange;">复查信息</div>
            <div class="box_cont" style="align-items: center;">
                <p class="mright">金额：</p>
                <input v-on:focus="heighOn" type="number" class="box_input" placeholder="请输入整改金额(元)" v-model="money">
            </div>
            <div class="box_cont mtop">
                <p class="mright">备注：</p>
                <textarea v-on:focus="heighOn" rows="5" class="box_txt" placeholder="请输入备注" v-model="remarks"></textarea>
            </div>
            <div class="box_cont mtop">
                <p class="mright">附件：</p>
                <unphoto v-on:imagesUrl="saveUrl"></unphoto>
            </div>
            <div class="box_cont mtop">
                <p class="mright">状态：</p>
                <yd-radio-group v-model="radio" color="#1D91E6">
                    <yd-radio val="0">通过</yd-radio>
                    <yd-radio val="1">不通过</yd-radio>
                </yd-radio-group>
            </div>
            <yd-button @click.native="saveData" size="large" color="#fff" bgcolor="#1D91E6">保存</yd-button>
        </div>
    </yd-layout>
</template>

<script>
    import {api_camera_recheck,api_camera_recheckinfo} from "../../components/APIKeys"
    import unphoto from '../../components/unphoto.vue'
    export default {
        name: "CameraRecheck",
        components:{unphoto},
        data(){
            return{
                ssp_id:window.localStorage.getItem('ssp_id'),//本条复查信息的ID
                // token:window.localStorage.getItem('token'),
                correctname:window.localStorage.getItem('correctname'),
                data:[],//复查页面的信息
                money:null,//复查金额
                remarks:'',//复查备注
                photoUrl:'',//复查图片
                radio:1,
            }
        },
        mounted(){
            this.doLoadRecheck();
        },
        methods:{
            //将子组件的值传过来
            saveUrl:function(data){
                this.photoUrl = data;
            },
            //获取复查页面信息
            doLoadRecheck(){
                this.$showDialog();
                let jsonData = {
                  // token:this.token,
                  ssp_id:this.ssp_id,
              }
              this.$post(api_camera_recheckinfo, jsonData).then(res=>{
                  this.$showDialog(false);
                  if(res.errno == 0){
                      this.data = res.data || [];
                  }else{
                      this.$showError('获取信息失败');
                  }
              })
            },
            //点击保存
            saveData(){
                if(this.money == null || this.money == ''){
                    this.$showError('请填写金额');
                    return false;
                }else if(this.remarks == ''){
                    this.$showError('请填写备注');
                    return false;
                }else if(this.photoUrl == ''){
                    this.$showError('请上传附件');
                    return false;
                }
                this.$showDialog();
                // return;
                let jsonData = {
                    // token:this.token,
                    id:this.data[0].ID,
                    ssp_id:this.data[0].SSP_ID,
                    fc_money:this.money,
                    fc_remarks:this.remarks,
                    state:this.radio,
                    fc_attachment:this.photoUrl,
                };
                this.$post(api_camera_recheck, jsonData).then(res=>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                       this.$showSuccess('保存成功');
                        window.history.go(-2);
                    } else{
                       this.$showError('保存失败');
                   }
                });
            },
            //防止键盘遮挡
            heighOn: function() {
                if (/Android/gi.test(navigator.userAgent)) {
                    window.addEventListener('resize', function () {
                        if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
                            window.setTimeout(function () {
                                document.activeElement.scrollIntoViewIfNeeded();
                            }, 0);
                        }
                    })
                }
            },
            //点击放大图片
            onPressLightbox(e, img) {
                let images = [
                    {url: img},
                ];
                this.$showLightbox(images);
                // fancyBox(e.target, images);
            },

        }
    }
</script>

<style scoped>
    .box_contc{
        margin:.24rem;
        padding: .24rem;
        background: #fff;
    }
    .box_titlec{
        font-size: .34rem;
        margin-bottom: .1rem;
        /*border-bottom: 1px solid #d9d9d9;*/
    }
    .gray{
        color: #909090;
    }
    .box_correct_img{
        height: 3rem;
    }
    .box_correct_img img{
        width: 100%;
        height: 100%;
    }
    .box_form{
        padding: .24rem;
        margin: .24rem;
        background: #fff;
    }
    .box_cont{
        display: flex;
        /*align-items: center;*/
    }
    .box_input{
        height: 0.8rem;
        flex: 1;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        border-radius:5px;
    }
    .box_txt{
        flex: 1;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        padding: .2rem;
    }
    .mtop{
        margin-top: .2rem;
    }
    .mright{
        margin-right: .2rem;
        white-space:nowrap;
    }
    .flex{
        display: flex;
    }
    .flex_image{
        flex: 1;
        display: flex;
        align-items: center;
    }
    .flex_image_img{
        width: calc(107% / 3);
        height: 1.8rem;
    }
    .flex_image_img img{
        width: 100%;
        height: 100%;
    }
</style>
<style>
    .box_recheck .yd-radio>input[type=radio]:checked+.yd-radio-icon{
        margin-bottom: .03rem;
    }
    .box_recheck .yd-radio-icon{
        margin-bottom: .03rem;
    }
    .box_recheck .yd-radio{
        padding-right: 1rem;
    }
</style>



